<template>
	<div class="terminal fade">
		<div style="position:relative">
			<div class="header">
				<h4>{{ title }}</h4>
				<ul class="shell-dots">
					<li class="red"></li>
					<li class="yellow"></li>
					<li class="green"></li>
				</ul>
			</div>

			<div
				id="terminalWindow"
				style="position:absolute;top:0;left:0;right:0;overflow:auto;z-index:1;margin-top:30px;max-height:643px;"
				ref="terminalWindow"
			>
				<div class="terminal-window" @click="handleFocus">
					<p>Welcome to {{ title }}.</p>
					<p>
						<span class="prompt"></span>
						<span class="cmd">cd {{ title }}</span>
					</p>
					<transition-group name="component-fade" mode="out-in">
						<p v-for="(item, index) in messageList" :key="index">
							<span v-if="item.time" class="time">{{ item.time }}</span>
							<span v-if="item.label" :class="item.type">{{ item.label }}</span>
							<span class="cmd" v-if="!item.message.list">{{
								item.message
							}}</span>
							<span class="cmd" v-else>
								<span>{{ item.message.text }}</span>
								<ul>
									<li v-for="(li, idx) in item.message.list" :key="idx">
										<span v-if="li.label" :class="li.type"
											>{{ li.label }}:</span
										>
										<pre>{{ li.message }}</pre>
									</li>
								</ul>
							</span>
						</p>
					</transition-group>

					<p v-if="actionResult">
						<span class="cmd">{{ actionResult }}</span>
					</p>
					<p
						class="terminal-last-line"
						ref="terminalLastLine"
						@click="handleFocus"
					>
						<span class="prompt" v-if="lastLineContent === '&nbsp'"
							>{{ title }}/</span
						>
						<span>{{ inputCommand }}</span>
						<span :class="lastLineClass" v-html="lastLineContent"></span>
						<input
							v-model.trim="inputCommand"
							:disabled="lastLineContent === '.'"
							autofocus="true"
							type="text"
							@keyup="handleCommand($event)"
							ref="inputBox"
							class="input-box"
							aria-label="command"
						/>
					</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'VTerminal',
  data () {
    return {
      title: '一个坏掉的番茄',
      messageList: [],
      actionResult: '',
      lastLineContent: '.',
      inputCommand: '',
      supportingCommandList: '',
      historyIndex: 0,
      commandHistory: []
    }
  },
  props: {
    restart: {
      required: false,
      default: 'restart'
    },
    commandList: {
      required: false,
      default: () => ({})
    },
    taskList: {
      required: false,
      default: () => ({})
    }
  },
  computed: {
    lastLineClass () {
      if (this.lastLineContent === '&nbsp') {
        return 'cursor'
      } else if (this.lastLineContent === '.') {
        return 'loading'
      }
      return 'loading'
    }
  },
  created () {
    this.supportingCommandList = [
      ...Object.keys(this.commandList),
      ...Object.keys(this.taskList)
    ]
    this.handleRun(this.restart).then(() => {
      this.pushToList({
        type: 'system',
        label: 'System',
        message: 'Type "help" to get a supporting command list.'
      })
      this.pushToList({
        type: 'system',
        label: 'System',
        message: 'Type "back" to return to the main page.'
      })
      this.handleFocus()
    })
  },
  methods: {
    handleFocus () {
      this.$refs.inputBox.focus()
    },
    handleCommand (e) {
      if (e.keyCode !== 13) {
        this.handlekeyEvent(e)
        return
      }
      this.commandHistory.push(this.inputCommand)
      this.historyIndex = this.commandHistory.length
      this.pushToList({ message: `$ ${this.inputCommand} ` })
      if (!this.inputCommand) {
        return
      }
      this.inputCommand = this.inputCommand.toLowerCase()
      const commandArr = this.inputCommand.split(' ')
      if (commandArr[0] === 'back') {
        this.pushToList({
          type: 'system',
          label: 'System',
          message: 'Jumping page...'
        })
        window.history
          ? window.history.go(-1)
          : (location.href = 'http://tomotoes.com/')
      } else if (commandArr[0] === 'help') {
        this.printHelp(commandArr[1])
      } else if (this.commandList[this.inputCommand]) {
        this.commandList[this.inputCommand].messages.forEach(item => {
          this.pushToList(item)
        })
      } else if (this.taskList[this.inputCommand.split(' ')[0]]) {
        this.handleRun(this.inputCommand.split(' ')[0], this.inputCommand)
      } else {
        this.pushToList({
          type: 'system',
          label: 'System',
          message: 'Unknown Command.'
        })
        this.pushToList({
          type: 'system',
          label: 'System',
          message: 'Type "help" to get a supporting command list.'
        })
      }
      this.inputCommand = ''
      this.autoScroll()
      this.handleFocus()
    },
    handlekeyEvent (e) {
      switch (e.keyCode) {
        case 38:
          this.historyIndex =
						this.historyIndex === 0 ? 0 : this.historyIndex - 1
          this.inputCommand = this.commandHistory[this.historyIndex]
          break
        case 40:
          this.historyIndex =
						this.historyIndex === this.commandHistory.length
						  ? this.commandHistory.length
						  : this.historyIndex + 1
          this.inputCommand = this.commandHistory[this.historyIndex]
          break
        default:
          break
      }
    },
    handleRun (taskName, input) {
      this.lastLineContent = '.'
      return this.taskList[taskName][taskName](this.pushToList, input)
        .then(done => {
          this.pushToList(done)
          this.lastLineContent = '&nbsp'
        })
        .catch(error => {
          this.pushToList(
            error || {
              type: 'error',
              label: 'Error',
              message: 'Something went wrong!'
            }
          )
          this.lastLineContent = '&nbsp'
        })
    },
    pushToList (message) {
      this.messageList.push(message)
      this.autoScroll()
    },
    printHelp (input) {
      if (!input) {
        this.pushToList({ message: 'Here is a list of supporting command.' })

        this.supportingCommandList.forEach(command => {
          if (this.commandList[command]) {
            this.pushToList({
              type: 'success',
              label: command,
              message: `// ${this.commandList[command].description}`
            })
          } else {
            this.pushToList({
              type: 'success',
              label: command,
              message: `// ${this.taskList[command].description}`
            })
          }
        })
        this.pushToList({ message: 'Type "back" to return to the main page.' })
      } else {
        const command = this.commandList[input] || this.taskList[input]
        this.pushToList({ message: command.description })
      }
      this.autoScroll()
      this.handleFocus()
    },
    time () {
      return new Date()
        .toLocaleTimeString()
        .split('')
        .splice(2)
        .join('')
    },
    autoScroll () {
      this.$nextTick(() => {
        this.$refs.terminalWindow.scrollTop = this.$refs.terminalLastLine.offsetTop
      })
    }
  }
}
</script>

